'use client'

import { useState } from "react";
import { registerPublicLead } from "@/actions/contacts";

export default function Home() {
    const [status, setStatus] = useState<'idle' | 'loading' | 'success' | 'error'>('idle');

    async function handleSubmit(e: React.FormEvent<HTMLFormElement>) {
        e.preventDefault();
        setStatus('loading');

        const form = e.currentTarget;
        const formData = new FormData(form);

        // Anti-bot check (hp-field)
        if (formData.get('phone_check')) {
            setStatus('success'); // Fake success for bots
            return;
        }

        // Map legacy field names to action expectations
        const mappedData = new FormData();
        mappedData.append('firstName', formData.get('nombre') as string);
        mappedData.append('email', formData.get('correo') as string);

        const result = await registerPublicLead(mappedData);

        if (result?.success) {
            setStatus('success');
            form.reset();
        } else {
            setStatus('error');
        }
    }

    return (
        <div className="min-h-screen flex justify-center py-[40px] px-[20px] bg-[#fafafa]">
            <div className="container-legacy">
                <style jsx>{`
                    .container-legacy { 
                        max-width: 600px; 
                        background: white; 
                        padding: 40px; 
                        border-radius: 15px; 
                        box-shadow: 0 10px 25px rgba(0,0,0,0.05); 
                        text-align: center; 
                        width: 100%;
                        height: fit-content;
                    }
                    h1 { color: #2c3e50; font-size: 1.2rem; letter-spacing: 2px; text-transform: uppercase; margin-bottom: 5px; margin-top: 0; }
                    h2 { color: #5d6d7e; font-size: 1.8rem; margin-top: 0; }
                    .profesion { font-style: italic; color: #7f8c8d; margin-bottom: 30px; border-bottom: 1px solid #eee; padding-bottom: 20px; }
                    .manifiesto { font-size: 1.1rem; margin-bottom: 30px; }
                    .promo-box { background: #fdfefe; border: 1px dashed #bdc3c7; padding: 25px; border-radius: 10px; text-align: left; }
                    .promo-box h3 { text-align: center; margin-top: 0; color: #2c3e50; font-size: 1.1rem; }
                    ul { list-style: none; padding: 0; }
                    li { margin-bottom: 15px; position: relative; padding-left: 25px; color: #333; }
                    li::before { content: "•"; position: absolute; left: 0; color: #3498db; font-weight: bold; }
                    
                    .cta { margin-top: 30px; color: #2c3e50; }
                    .form-group { margin-bottom: 15px; text-align: left; }
                    input[type="text"], input[type="email"] { 
                        width: 100%; padding: 12px; border: 1px solid #ddd; border-radius: 8px; box-sizing: border-box; font-size: 1rem;
                    }
                    button { 
                        background-color: #2c3e50; color: white; border: none; padding: 15px 30px; border-radius: 8px; 
                        cursor: pointer; font-size: 1rem; font-weight: bold; width: 100%; transition: background 0.3s;
                    }
                    button:hover { background-color: #34495e; }
                    button:disabled { opacity: 0.7; cursor: not-allowed; }
                    .hp-field { display: none !important; }
                    .success-msg { color: #27ae60; font-weight: bold; margin-top: 20px; }
                    .error-msg { color: #e74c3c; font-size: 0.9rem; margin-top: 10px; }
                `}</style>

                <h1>WEB OFICIAL</h1>
                <h2>Ana Katherine Zepeda Pallacán</h2>
                <p className="profesion">Psicóloga – Maestra de Yoga – Meditación – Crioterapia</p>

                <div className="manifiesto">
                    <p>Navegando todo sobre salud mental, física y espiritual.</p>
                    <p><strong>Desde 2009 a la fecha</strong>, convencida de que menos es más.</p>
                </div>

                <div className="promo-box">
                    <h3>REGÍSTRATE HOY Y RECIBE:</h3>
                    <ul>
                        <li>Audio para aprender a meditar.</li>
                        <li>Datos prácticos de salud.</li>
                        <li>Reflexiones para que no sientas que eres el único loco o loca que busca algo mejor de lo que hemos creado.</li>
                        <li>Acceso anticipado a lanzamientos, regalos y promociones.</li>
                    </ul>
                </div>

                <div className="cta">
                    <p><strong>Te leo</strong></p>

                    {status === 'success' ? (
                        <div className="success-msg">
                            ¡Gracias por registrarte! Pronto recibirás noticias nuestras.
                        </div>
                    ) : (
                        <form onSubmit={handleSubmit}>
                            <input type="text" name="phone_check" className="hp-field" tabIndex={-1} autoCorrect="off" />

                            <div className="form-group">
                                <input type="text" name="nombre" placeholder="Tu nombre" required disabled={status === 'loading'} />
                            </div>
                            <div className="form-group">
                                <input type="email" name="correo" placeholder="Tu correo electrónico" required disabled={status === 'loading'} />
                            </div>

                            <button type="submit" disabled={status === 'loading'}>
                                {status === 'loading' ? 'PROCESANDO...' : 'REGISTRARME'}
                            </button>
                            {status === 'error' && (
                                <p className="error-msg">Ocurrió un error. Por favor intenta de nuevo.</p>
                            )}
                        </form>
                    )}

                    <p style={{ fontSize: '0.8rem', color: '#95a5a6', marginTop: '15px' }}>
                        Acepto la política de privacidad
                    </p>

                    {/* Discreto link de acceso */}
                    <Link href="/login" style={{ fontSize: '0.7rem', color: '#bdc3c7', textDecoration: 'none', display: 'block', marginTop: '20px', textTransform: 'uppercase', letterSpacing: '1px' }}>
                        Acceso Alumnos
                    </Link>
                </div>
            </div>
        </div>
    );
}
